/**
 *
 * Tab Style
 *
 * @author Takuto Yanagida
 * @version 2021-11-11
 *
 */


@use 'scroll';
@use 'stack';


div.tab-scroll {
	@include scroll.style-scroll;
	@include scroll.style-scroll-decoration-tab;

	@media print {
		@include scroll.style-scroll-print
	}
}

div.tab-stack {
	@include stack.style-stack;
	@include stack.style-stack-decoration-tab;

	@media print {
		@include stack.style-stack-print
	}
}
